body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.app {
  width: 90%;
  min-height: 100vh;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

main {
  flex-grow: 1;
  display: flex;
}
aside {
  min-width: 150px;
}

footer {
  min-height: 30px;
  background-color: black;
  color: white;
  text-align: center;
  text-transform: uppercase;
  line-height: 30px;
  letter-spacing: 2px;
}

header {
  flex-basis: 40vh;
  position: relative;
  overflow: hidden;
}

header img {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  min-width: 100%;
  min-height: 100%;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0px;
}

a {
  display: block;
  text-decoration: none;
  text-transform: uppercase;
  color: black;
  font-size: 20px;
  margin: 5px 0;
  transition: 0.4s;
  padding-left: 15px;
}
nav.main a:hover {
  padding-left: 25px;
}

nav.main li:nth-child(1) .active {
  padding-left: 5px;
  border-left: 10px solid green;
}
nav.main li:nth-child(2) .active {
  padding-left: 5px;
  border-left: 10px solid red;
}
nav.main li:nth-child(3) .active {
  padding-left: 5px;
  border-left: 10px solid blue;
}
nav.main li:nth-child(4) .active {
  padding-left: 5px;
  border-left: 10px solid orange;
}

section {
  flex-grow: 1;
}
.article {
  width: 80%;
  margin: 0 auto;
}

.contact {
  text-align: center;
}
.contact h3 {
  text-transform: uppercase;

  margin: 15px 0;
}
.contact textarea {
  width: 400px;
  height: 200px;
  text-align: center;
  line-height: 1.5;
  letter-spacing: 1px;
}
.contact button {
  display: block;
  padding: 10px 20px;
  margin: 10px auto 20px;
}


